parallax {
	overflow: hidden;
	letter-spacing: -2px;
	line-height: 0.8;
	margin: 0;
	white-space: nowrap;
	display: flex;
	flex-wrap: nowrap;
 }
 
 .parallax .scroller {
	font-weight: 600;
	text-transform: uppercase;
	font-size: 1.5em;
	display: flex;
	white-space: nowrap;
	display: flex;
	flex-wrap: nowrap;
 }
 
 .parallax span {
	display: block;
	margin-right: 30px;
 }
 